import React from 'react';
import '../css/Contact.scss';
import Data from "../messages/Text.json"
import ContactAPI from "../api/ContactAPI"
import InstagramPicture from '../images/instagram.png';
import LinkedInPicture from '../images/linkedIn.png';
import GitHubPicture from '../images/github.png';

const Contact = () => {
    const instagramUrl = "https://www.instagram.com/khangaroooo/";
    const githubUrl = "https://github.com/KhangNguyen3000";
    const linkedInUrl = "https://www.linkedin.com/in/khanghnguyen1999/";
    const [email, setEmail] = React.useState('');
    const [subject, setSubject] = React.useState('');
    const [text, setText] = React.useState('');

    const socialDisplay = () => {
        return(
            <React.Fragment>
                <div className="container">
                    <span className="important">{Data.Contact.Title}</span>
                </div>
                <div className="social-containers">
                    <div className="social-picture-container">
                        <img src={InstagramPicture} className="social-picture" alt="Khang Nguyen" onClick={() => {
                            window.open(instagramUrl, "_blank")
                        }}/>
                    </div>
                    <div className="social-picture-container">
                        <img src={LinkedInPicture} className="social-picture" alt="Khang Nguyen" onClick={() => {
                            window.open(linkedInUrl, "_blank")
                        }}/>
                    </div>
                    <div className="social-picture-container">
                        <img src={GitHubPicture} className="social-picture" alt="Khang Nguyen" onClick={() => {
                            window.open(githubUrl, "_blank")
                        }}/>
                    </div>
                </div>
            </React.Fragment>
        );
    }

    const getInContact = () => {
        return(
            <React.Fragment>
                <div className="container">
                    <div className="message-container">
                        <div>
                            <span className="important">{Data.Contact.leaveAMessage}</span>
                        </div>
                        <div className="display-flex">
                            <div>
                                Email<input className="m-input" value={email} onChange={event => setEmail(event.target.value)}/>   
                            </div>
                            <div>
                                Subject<input className="m-input" value={subject} onChange={event => setSubject(event.target.value)}/>
                            </div>
                        </div>
                        Message
                        <div className="input-div" style={{padding:".5em 0 0 0"}}>
                            <textarea value={text} className="input" onChange={event => setText(event.target.value)}/>
                        </div>
                        <div className="button-container-c">
                            <div className="button-c" onClick={()=>{
                                ContactAPI.sendMessage(email,subject,text, setEmail, setSubject, setText);
                            }}>
                                Submit
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }


    return (
        <React.Fragment>
            {socialDisplay()}
            {getInContact()}     
            <div className="container">
            Or email me at nguyenh.khang.1999@gmail.com
            </div>
        </React.Fragment>
    );
}

export default Contact;
