import React from 'react';
import '../css/Project.scss';
import ProjectsAPI from '../api/ProjectsAPI';
import Data from "../messages/Text.json"

interface Project {
    name: string;
    description: string;
    html_url: string;
};

const Projects = () => {

    const [projects, setProjects] = React.useState<Project[]>([]);

    const loopThroughGit = async () => {
        const data = (await ProjectsAPI.getProjects());
        setProjects(data);
    };

    if(!projects.length){
        loopThroughGit()
    }

    const projectDisplay = (project: Project, index: number) => {
        return(
            <div key={index} className="project-container" onClick={() => {
                window.open(project.html_url, "_blank")
            }}>
                <h3>
                    {project.name}
                </h3>
                <p className="description">
                    {project.description}
                </p>
            </div>
        );
    };


    return (
        <React.Fragment>
            <div className="container">
                <span className="important">{Data.Projects.Title}</span>
            </div>
            <div className="container">
                <p className="text">{Data.Projects.Introduction}</p>
            </div>
            <div className='page-container'>
                {projects ? projects.map(projectDisplay) : null}
            </div>
        </React.Fragment>
    );
};

export default Projects;
