import React, { Dispatch, SetStateAction } from 'react';
import '../css/Home.scss';
import MainPicture from '../images/Circle.jpg';
import Data from "../messages/Text.json"

const Home = (props:{  
    setPage: Dispatch<SetStateAction<string>>; 
    setShow:Dispatch<SetStateAction<boolean>>;   
    pages: string[];
}) => {


    const onPageClick = () => {
        props.setShow(false)
        setTimeout(()=>{props.setPage(props.pages[1])},100);
    }

    return (
        <React.Fragment>
            <div className="image-container">
                <img src={MainPicture} className="image" alt="Khang Nguyen"/>
            </div>
            <div className="container">
                <span className="important">{Data.Home.Title}</span>
            </div>
            <div className="container">
                <p className="text">{Data.Home.Introduction}</p>
            </div>
            <div className="container" onClick={onPageClick}>
                <span className="button">
                Click Here. Right Now.
                </span>
            </div>
        </React.Fragment>
    );
}

export default Home;
