import React, { SetStateAction, Dispatch } from 'react';
import '../css/App.scss';

const Navbar = (props: {
    page: string;
    setPage: Dispatch<SetStateAction<string>>; 
    pages: string[];
    setShow: Dispatch<SetStateAction<boolean>>;
}) => {

    const displayButton = (page: string, index: number) => {

        const onPageClick = () => {
            if (page !== props.page) {
                props.setShow(false)
                setTimeout(()=>{props.setPage(page)},100);
            }
        }

        const buttonId = page + "NavButton";
        return (
            <div key={index} id={buttonId} onClick={onPageClick} className={["button", (page === props.page) ? "selected" : null].join(" ")}>
                <div className="center">
                    {page}
                </div>
            </div>
        );
    }



    return (
        <div className="navbar">
            <div className="button-container">
                <div className="title">
                    Khang Nguyen
                </div>
                {props.pages.map(displayButton)}
            </div>
        </div>
    );
    }

export default Navbar;
