import React, { Dispatch, SetStateAction } from 'react';
import '../css/Biography.scss';
import Data from '../messages/Resume.json';
import Messages from "../messages/Text.json"
 // http://www.objgen.com/json/models/ptpn for the model

const json = JSON.parse(JSON.stringify(Data));

const SecondLevel = (props:{
    json: any,
    activeList: string[],
    setActiveList: Dispatch<SetStateAction<string[]>>
}) => {
    let jsx = []
    let first = true;
    for (let i in props.json) {
        jsx.push(
        <div key={i} className={[first ? 'no-border' : null,'second-level'].join(" ")}>
            <div className='second-level-button-container'>
                <div className='second-level-button' onClick={()=>{
                            let tmp = [...props.activeList];
                            if(props.activeList.includes(i)){
                                const index = tmp.indexOf(i);
                                tmp.splice(index, 1);
                                props.setActiveList(tmp)
                            }else{
                                tmp.push(i);
                                props.setActiveList(tmp);
                            }
                        }}>
                    <h2>{i}</h2>
                </div>
            </div>
            {props.activeList.includes(i) ? (
                <div className='zone-padding'>
                    <ThirdLevel json={props.json[i]}/>            
                </div>
            ) : null}
        </div>
        );
        first = false;
    }
    return(
        <div className='second-column'>
            {jsx}
        </div>
    );
};

const ThirdLevel = (props:{
    json: any
}) => {
    let jsx = []

    for (let i in props.json) {
        jsx.push(
            <div key={i} className='third-level'>
                • {props.json[i]}
            </div>
        );
    }

    return(
        <React.Fragment>
            {jsx}
        </React.Fragment>
    );
};


const ExpandCloseButtons = (props:{
    activeList: string[],
    setActiveList: Dispatch<SetStateAction<string[]>>
}) => {

    const checkExpansion = (json: any, layerPresence: boolean, layers: number) => {
        if(layers > 0){
            for(let i in json){
                if (!props.activeList.includes(i)){
                    layerPresence = false;
                    return layerPresence;
                }  
                layerPresence = checkExpansion(json[i], layerPresence, layers-1)
            }
        }
        return layerPresence;
    }

    const expandedList: React.SetStateAction<string[]> = []

    const expandActiveList = (json: any, layers: number) => {
        if(layers > 0){
            for(let i in json){
                expandedList.push(i);
                expandActiveList(json[i], layers-1);
            }
        }
    };

    expandActiveList(json, 2);

    return (
        <React.Fragment>
            <div className="ec-container">
                {!checkExpansion(json, true, 2) && 
                    <div className="ec-button-container">
                        <div className="ec-button" onClick={()=>{props.setActiveList(expandedList)}}>
                            <span>Expand All</span>
                        </div>
                    </div>
                }
                {props.activeList.length ? 
                    <div className="ec-button-container">
                        <div className="ec-button" onClick={()=>{props.setActiveList([])}}>
                            <span>Collapse All</span>
                        </div>
                    </div> 
                : null }
            </div>
        </React.Fragment>
    )

}


const Biography = (props:{
    activeList: string[],
    setActiveList: Dispatch<SetStateAction<string[]>>
}) => {

    const populateResume = () => {

        let jsx = [];

        for (let i in json) {
            jsx.push(
                <div key={i} className='first-level'>
                    <div className='button-container'>
                        <div className='button' onClick={()=>{
                            let tmp = [...props.activeList];
                            if(props.activeList.includes(i)){
                                const index = tmp.indexOf(i);
                                tmp.splice(index, 1);
                                props.setActiveList(tmp)
                            }else{
                                tmp.push(i);
                                props.setActiveList(tmp);
                            }
                        }}>
                            <h1>{i}</h1>
                        </div>
                    </div>
                    {props.activeList.includes(i) ? <SecondLevel json={json[i]} activeList={props.activeList} setActiveList={props.setActiveList}/> : null}
                </div>
            );
        }

        return (
            <React.Fragment>
                {jsx}
            </React.Fragment>
        );
    };

    return (
        <React.Fragment>
            <div className="container">
                <span className="important">{Messages.Biography.Title}</span>
            </div>
            <div className="container">
                <p className="text">{Messages.Biography.Introduction}</p>
            </div>
            <div className="container text">
                <span className="rp-text">Institution: </span><span>{Messages.Biography.Institution}</span>
            </div>
            <div className="container">
                <span className="rp-text">Program: </span><span>{Messages.Biography.Education}</span>
            </div>
            <ExpandCloseButtons activeList={props.activeList} setActiveList={props.setActiveList}/>
            {populateResume()}
        </React.Fragment>
    );
};

export default Biography;
