import React from 'react';
import '../css/App.scss';
import Navbar from './Navbar';
import Home from '../components/Home';
import Biography from '../components/Biography';
import Projects from '../components/Projects';
import Contact from '../components/Contact';

const App = () => {
  const pages = ["Home", "Resume", "Projects", "Contact Me"];
  const [page, setPage] = React.useState("Home");
  const [show, setShow] = React.useState(false);
  const [activeList, setActiveList]  = React.useState([] as string[]);
  const pageComponents = [<Home setPage={setPage} setShow={setShow} pages={pages}/>, <Biography activeList={activeList} setActiveList={setActiveList}/>, <Projects/>, <Contact/>];

  React.useEffect(()=>{
    setShow(true);
  },[page]);

  return (
    <React.Fragment>
      <Navbar page={page} setPage={setPage} pages={pages} setShow={setShow}/>
      <div className={show ? "show" : "hide"}>
        {pageComponents[pages.indexOf(page)]}
      </div>
    </React.Fragment>
  );
}

export default App;
